/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
img {
  max-width: 100%;
}
* {
  box-sizing: border-box;
  color: #fff;
  font-family: "Montserrat", sans-serif !important;
}

body {
  background-color: #000;
}

.color-error {
  color: #ff0000;
}

.color-yellow {
  color: #ffff00;
}

.color-blue {
  color: #1af;
}

.rsa {
  position: relative;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.container {
  max-width: 980px;
  width: 100%;
  display: block;
  margin: 0 auto;
}

.header {
  background-color: #000;
  border-bottom: 1px solid #fff;
}

.header__logo-container {
  float: left;
  width: 120px;
  text-align: center;
  padding: 10px 0;
}

.header__logo-container span {
  text-transform: uppercase;
  line-height: 0.85;
  display: block;
  color: #fff;
  -webkit-transition: color 0.2s linear;
  transition: color 0.2s linear;
}

.header__logo-link {
  display: block;
  text-decoration: none;
}

.header__logo-link:hover .header__logo-white {
  color: #1af;
}

.header__logo-link:hover .header__logo-onblack {
  color: #ffff00;
}

.header__logo-white {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: -1px;
}

.header__logo-onblack {
  font-size: 15px;
}

.nav__list {
  font-style: 0;
  text-align: right;
}

.nav__item {
  display: inline-block;
  vertical-align: top;
}

.nav__link {
  display: block;
  text-decoration: none;
  padding: 20px;
  color: #fff;
  -webkit-transition: color 0.2s linear;
  transition: color 0.2s linear;
}

.nav__link:hover {
  color: #1af;
}

.main-content .container {
  border: 1px solid #fff;
  border-top: none;
  padding: 40px 0;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #fff;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: #fff;
}

:-ms-input-placeholder {
  /* IE 10+ */
  color: #fff;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: #fff;
}

.search__container {
  text-align: center;
  margin: 0px auto 40px;
}

.search__input {
  display: block;
  border: 1px solid #fff;
  background-color: transparent;
  color: #1af;
  max-width: 400px;
  width: 100%;
  padding: 10px 15px;
  margin: 0px auto;
  font-size: 14px;
  -webkit-transition: all 0.15s linear;
  transition: all 0.15s linear;
  outline: none;
}

.search__input:focus {
  border-color: #1af;
}

.film__list {
  font-size: 0;
}

.film__user {
  color: #fff;
  font-size: 18px;
  text-align: center;
  margin: 20px;
}

.film__image {
  height: 400px;
  width: 100%;
}

.film__item {
  display: inline-block;
  vertical-align: top;
  width: 30%;
  padding: 10px;
  border: 1px solid #fff;
  overflow: hidden;
  margin: 0px 16px 20px;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.film__item:hover {
  border-color: #1af;
  box-shadow: 0px 0px 20px 5px rgba(255, 255, 255, 0.25);
}

.film__link {
  display: block;
  text-decoration: none;
}

.film__title {
  font-size: 18px;
  margin: 0 0 10px;
  height: 36px;
}

.film__description {
  font-size: 12px;
  margin-top: 15px;
}

.film .bookmark {
  float: right;
}

.film .bookmark svg {
  font-size: 22px;
}

.film .bookmark svg path {
  fill: #fff;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.film .bookmark:hover svg path {
  fill: #11aaff !important;
}

.film .bookmark.added svg path {
  fill: #ffff00;
}

.pagination__container {
  margin-top: 40px;
}

.pagination__list {
  text-align: center;
}

.pagination__item {
  font-size: 18px;
  padding: 0 5px;
  margin: 0 5px 10px;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  color: #fff;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.pagination__item:hover {
  color: #1af;
}

.pagination__item.current {
  color: #ffff00 !important;
  cursor: default;
}

.info {
  padding: 0 20px;
}

.info .color-yellow {
  color: #ffff00;
}

.info .color-blue {
  color: #1af;
}

.info__container {
  padding-left: 60px;
}

.info__title {
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
  padding: 0 40px;
}

.info__item {
  display: inline-block;
  vertical-align: top;
  width: 60%;
}

.info__image-container {
  text-align: center;
  width: 35%;
}

.info__link {
  color: #ffff00;
  text-decoration: none;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
}

.info__link:hover {
  color: #1af;
}

.info__description {
  margin-bottom: 15px;
}

.info__overview {
  line-height: 1.5;
  font-size: 16px;
  margin: 25px 0 0;
}

.info__button {
  outline: none;
  background-color: transparent;
  padding: 10px 20px;
  cursor: pointer;
  border: 1px solid #fff;
  box-shadow: 0px 0px 20px 5px rgba(255, 255, 255, 0.5);
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.info__button .bookmark {
  display: inline-block;
  vertical-align: middle;
}

.info__button-container {
  margin-top: 40px;
}

.info__button-text {
  font-size: 16px;
  display: inline-block;
  vertical-align: bottom;
}

.info__button svg {
  font-size: 18px;
  margin-right: 10px;
}

.info__button svg path {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.info__button:hover {
  box-shadow: 0px 0px 20px 5px rgba(17, 170, 255, 0.5) !important;
}

.info__button:hover svg path {
  fill: #1af !important;
}

.info__button.added {
  box-shadow: 0px 0px 20px 5px rgba(255, 255, 0, 0.5);
}

.info__button.added svg path {
  fill: #ffff00;
}

.notification {
  max-width: 320px;
  width: 100%;
  position: fixed;
  top: 57px;
  right: 25px;
}

.notification__container {
  border: 1px solid #fff;
  padding: 15px 20px;
  margin-bottom: 10px;
  cursor: pointer;
  background-color: #000;
}

.notification__title {
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
  display: block;
  font-size: 16px;
}

.notification__message {
  font-size: 14px;
  line-height: 1.3;
}

.recommendations__title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 30px;
}

